	module.exports = {"data":{"markdownRemark":{"html":"<h1>Format Specifiers</h1>\n<p>The % specifiers that you can use in ANSI C are:</p>\n<table>\n<thead>\n<tr>\n<th align=\"center\">Specifier</th>\n<th align=\"center\">Used For</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"center\">%c</td>\n<td align=\"center\">a single character</td>\n</tr>\n<tr>\n<td align=\"center\">%s</td>\n<td align=\"center\">a string</td>\n</tr>\n<tr>\n<td align=\"center\">%d</td>\n<td align=\"center\">a decimal integer</td>\n</tr>\n<tr>\n<td align=\"center\">%o</td>\n<td align=\"center\">an octal (base 8) integer</td>\n</tr>\n<tr>\n<td align=\"center\">%x</td>\n<td align=\"center\">a hexadecimal (base 16) integer</td>\n</tr>\n<tr>\n<td align=\"center\">%p</td>\n<td align=\"center\">an address (or pointer)</td>\n</tr>\n<tr>\n<td align=\"center\">%f</td>\n<td align=\"center\">a floating point number for floats</td>\n</tr>\n<tr>\n<td align=\"center\">%e</td>\n<td align=\"center\">a floating point number in scientific notation</td>\n</tr>\n<tr>\n<td align=\"center\">%E</td>\n<td align=\"center\">a floating point number in scientific notation</td>\n</tr>\n<tr>\n<td align=\"center\">%%</td>\n<td align=\"center\">The % symbol!</td>\n</tr>\n</tbody>\n</table>","fields":{"slug":"/c/format-specifiers/"},"frontmatter":{"title":"Format Specifiers"}}},"pathContext":{"slug":"/c/format-specifiers/"}}

